<template>
	<view class="fans-group-manage">
		<view class="header">
			<view class="title">粉丝群链接管理</view>
			<view class="desc">为不同用户组设置不同的粉丝群链接</view>
		</view>
		
		<view class="content">
			<!-- 加载中 -->
			<view class="loading-container" v-if="isLoading">
				<uni-icons type="spinner-cycle" size="40" color="#399bfe"></uni-icons>
				<text class="loading-text">加载中...</text>
			</view>
			
			<!-- 空状态 -->
			<view class="empty-container" v-else-if="isEmpty">
				<uni-icons color="#399bfe" type="info-filled" size="60"></uni-icons>
				<text class="empty-text">暂无粉丝群链接</text>
				<text class="empty-desc">点击下方按钮添加不同用户组的粉丝群链接</text>
			</view>
			
			<!-- 链接列表 -->
			<view class="link-list" v-else>
				<view class="link-item" v-for="(link, index) in fansGroupLinks" :key="index">
					<view class="link-content">
						<view class="link-header">
							<view class="link-name">{{ link.name }}</view>
							<view class="link-actions">
								<view class="action-btn edit" @click="openEditLinkModal(link, index)">
									<uni-icons type="compose" size="18" color="#399bfe"></uni-icons>
								</view>
								<view class="action-btn delete" @click="deleteLink(index)">
									<uni-icons type="trash" size="18" color="#ff5a5f"></uni-icons>
								</view>
							</view>
						</view>
						
						<view class="link-body">
							<view class="link-id" @click="copyGroupId(link.groupId)">
								<text class="id-label">群ID:</text>
								<text class="id-value">{{ link.groupId.substring(0, 20) }}...</text>
								<view class="copy-icon">
									<uni-icons type="copy" size="14" color="#666"></uni-icons>
								</view>
							</view>
							
							<view class="link-desc" v-if="link.desc">
								<text class="desc-label">描述:</text>
								<text class="desc-value">{{ link.desc }}</text>
							</view>
							
							<view class="link-updated" v-if="link.updatedAt">
								<text class="updated-label">更新时间:</text>
								<text class="updated-value">{{ formatTime(link.updatedAt) }}</text>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- 添加按钮 -->
		<view class="add-button" @click="openAddLinkModal">
			<uni-icons type="plusempty" size="20" color="#ffffff"></uni-icons>
			<text class="add-text">添加新链接</text>
		</view>
		
		<!-- 编辑/添加模态框 -->
		<view class="modal-container" v-if="showLinkModal">
			<view class="modal-mask" @click="cancelLinkEdit"></view>
			<view class="modal-content">
				<view class="modal-header">
					<text class="modal-title">{{ isEditMode ? '编辑粉丝群链接' : '添加粉丝群链接' }}</text>
					<view class="modal-close" @click="cancelLinkEdit">
						<uni-icons type="closeempty" size="22" color="#666"></uni-icons>
					</view>
				</view>
				<view class="modal-body">
					<view class="form-group">
						<text class="form-label">用户组名称 <text class="required">*</text></text>
						<input 
							class="form-input"
							v-model="editingLinkName"
							placeholder="例如: VIP用户、普通用户等"
						/>
					</view>
					
					<view class="form-group">
						<text class="form-label">粉丝群ID <text class="required">*</text></text>
						<textarea 
							class="form-textarea"
							v-model="editingLinkId"
							placeholder="请输入粉丝群ID"
							auto-height
						></textarea>
						<text class="form-tip">该ID将用于识别不同用户组的粉丝群</text>
					</view>
					
					<view class="form-group">
						<text class="form-label">描述</text>
						<textarea 
							class="form-textarea"
							v-model="editingLinkDesc"
							placeholder="可选：描述该用户组的特点"
							auto-height
						></textarea>
					</view>
				</view>
				<view class="modal-footer">
					<button class="btn-cancel" @click="cancelLinkEdit">取消</button>
					<button class="btn-save" @click="saveLinkSettings">保存</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script setup>
	import { ref, onMounted, onUnmounted } from 'vue';
	import { onShow } from '@dcloudio/uni-app'
	
	const sendOnApi = uniCloud.importObject('sendOn')
	
	// 粉丝群链接管理状态
	const fansGroupLinks = ref([])
	const isLoading = ref(false)
	const isEmpty = ref(false)
	
	// 编辑状态
	const showLinkModal = ref(false)
	const isEditMode = ref(false)
	const currentEditingIndex = ref(null)
	const editingLinkName = ref('')
	const editingLinkId = ref('')
	const editingLinkDesc = ref('')
	
	// 获取粉丝群链接列表
	const getGroupLinks = async () => {
		try {
			isLoading.value = true
			
			uni.showLoading({
				title: '加载中...',
				mask: true
			})
			
			const res = await sendOnApi.get()
			
			if (res && res.data && res.data.length > 0) {
				// 获取多用户粉丝群链接列表
				if (res.data[0].fansGroupLinks && Array.isArray(res.data[0].fansGroupLinks)) {
					fansGroupLinks.value = res.data[0].fansGroupLinks;
					isEmpty.value = fansGroupLinks.value.length === 0;
				} else {
					// 如果不存在，初始化为空数组
					fansGroupLinks.value = [];
					isEmpty.value = true;
				}
				
				console.log('多用户粉丝群链接:', fansGroupLinks.value)
			} else {
				console.error('获取粉丝群链接失败: 数据格式不正确')
				uni.showToast({
					icon: 'none',
					title: '获取数据失败'
				})
				isEmpty.value = true;
			}
		} catch (error) {
			console.error('获取粉丝群链接失败:', error)
			uni.showToast({
				icon: 'none',
				title: '获取数据失败'
			})
			isEmpty.value = true;
		} finally {
			isLoading.value = false
			uni.hideLoading()
		}
	}
	
	// 打开添加链接模态框
	const openAddLinkModal = () => {
		isEditMode.value = false
		currentEditingIndex.value = null
		editingLinkName.value = ''
		editingLinkId.value = ''
		editingLinkDesc.value = ''
		showLinkModal.value = true
	}

	// 打开编辑链接模态框
	const openEditLinkModal = (link, index) => {
		isEditMode.value = true
		currentEditingIndex.value = index
		editingLinkName.value = link.name || ''
		editingLinkId.value = link.groupId || ''
		editingLinkDesc.value = link.desc || ''
		showLinkModal.value = true
	}

	// 取消编辑
	const cancelLinkEdit = () => {
		showLinkModal.value = false
	}

	// 保存链接
	const saveLinkSettings = async () => {
		try {
			if (!editingLinkName.value.trim()) {
				return uni.showToast({
					icon: 'none',
					title: '用户组名称不能为空'
				})
			}
			
			if (!editingLinkId.value.trim()) {
				return uni.showToast({
					icon: 'none',
					title: '群ID不能为空'
				})
			}
			
			uni.showLoading({
				title: '保存中...',
				mask: true
			})
			
			// 构建链接对象
			const linkObj = {
				name: editingLinkName.value,
				groupId: editingLinkId.value,
				desc: editingLinkDesc.value,
				updatedAt: new Date().getTime()
			}
			
			// 备份当前链接列表
			const oldLinks = [...fansGroupLinks.value]
			
			// 取得当前设置参数
			const currentSettings = await sendOnApi.get()
			if (!currentSettings || !currentSettings.data || !currentSettings.data.length) {
				throw new Error('获取当前设置失败')
			}
			
			const currentData = currentSettings.data[0]
			
			// 更新本地状态
			if (isEditMode.value && currentEditingIndex.value !== null) {
				// 编辑模式 - 更新已有链接
				fansGroupLinks.value[currentEditingIndex.value] = linkObj
			} else {
				// 添加模式 - 添加新链接
				fansGroupLinks.value.push(linkObj)
			}
			
			// 更新服务器数据
			const res = await sendOnApi.updateFansGroupLinks(
				true,
				currentData.publishButton !== undefined ? currentData.publishButton : false,
				currentData.floatButton !== undefined ? currentData.floatButton : false,
				currentData.avatarClick !== undefined ? currentData.avatarClick : false,
				currentData.commentVisibility !== undefined ? currentData.commentVisibility : false,
				currentData.lotteryVisibility !== undefined ? currentData.lotteryVisibility : false,
				currentData.fans_group_id !== undefined ? currentData.fans_group_id : "",
				fansGroupLinks.value
			)
			
			console.log('粉丝群链接更新结果:', res)
			
			// 关闭模态框
			showLinkModal.value = false
			
			// 更新空状态
			isEmpty.value = fansGroupLinks.value.length === 0;
			
			uni.showToast({
				icon: "success",
				title: isEditMode.value ? '链接已更新' : '链接已添加',
				duration: 2000
			})
		} catch (error) {
			console.error('更新粉丝群链接失败:', error)
			// 出错时回滚
			fansGroupLinks.value = oldLinks
			
			uni.showToast({
				icon: "error",
				title: '操作失败',
				duration: 2000
			})
		} finally {
			uni.hideLoading()
		}
	}

	// 删除链接
	const deleteLink = async (index) => {
		uni.showModal({
			title: '确认删除',
			content: '确定要删除此用户组的粉丝群链接吗？',
			success: async (res) => {
				if (res.confirm) {
					try {
						uni.showLoading({
							title: '删除中...',
							mask: true
						})
						
						// 备份当前链接列表
						const oldLinks = [...fansGroupLinks.value]
						
						// 取得当前设置参数
						const currentSettings = await sendOnApi.get()
						if (!currentSettings || !currentSettings.data || !currentSettings.data.length) {
							throw new Error('获取当前设置失败')
						}
						
						const currentData = currentSettings.data[0]
						
						// 更新本地状态
						fansGroupLinks.value.splice(index, 1)
						
						// 更新服务器数据
						const res = await sendOnApi.updateFansGroupLinks(
							true,
							currentData.publishButton !== undefined ? currentData.publishButton : false,
							currentData.floatButton !== undefined ? currentData.floatButton : false,
							currentData.avatarClick !== undefined ? currentData.avatarClick : false,
							currentData.commentVisibility !== undefined ? currentData.commentVisibility : false,
							currentData.lotteryVisibility !== undefined ? currentData.lotteryVisibility : false,
							currentData.fans_group_id !== undefined ? currentData.fans_group_id : "",
							fansGroupLinks.value
						)
						
						console.log('删除粉丝群链接结果:', res)
						
						// 更新空状态
						isEmpty.value = fansGroupLinks.value.length === 0;
						
						uni.showToast({
							icon: "success",
							title: '链接已删除',
							duration: 2000
						})
					} catch (error) {
						console.error('删除粉丝群链接失败:', error)
						// 出错时回滚
						fansGroupLinks.value = oldLinks
						
						uni.showToast({
							icon: "error",
							title: '删除失败',
							duration: 2000
						})
					} finally {
						uni.hideLoading()
					}
				}
			}
		})
	}

	// 复制群ID
	const copyGroupId = (groupId) => {
		uni.setClipboardData({
			data: groupId,
			success: () => {
				uni.showToast({
					title: '已复制到剪贴板'
				})
			}
		})
	}
	
	// 格式化时间
	const formatTime = (timestamp) => {
		if (!timestamp) return '未知时间';
		
		const date = new Date(timestamp);
		const year = date.getFullYear();
		const month = String(date.getMonth() + 1).padStart(2, '0');
		const day = String(date.getDate()).padStart(2, '0');
		const hours = String(date.getHours()).padStart(2, '0');
		const minutes = String(date.getMinutes()).padStart(2, '0');
		
		return `${year}-${month}-${day} ${hours}:${minutes}`;
	}

	// 页面加载时获取数据
	onMounted(() => {
		getGroupLinks()
	})

	// 页面显示时刷新数据
	onShow(() => {
		getGroupLinks()
	})
</script>

